import { html , render , WebComponent , customElement , attr , attrState , state, css, ViewTemplate, ViewContext, asyncAppend, repeat, children } from '@lithium-framework/core';
import { PfWebComponent } from '../../../../PatternFlyLithium/unofficial-pf-v5-wc/src/models';
import { PfModalBox } from 'unofficial-pf-v5-wc';
import '@lithium-framework/router-element';
import 'unofficial-pf-v5-wc';
import 'unofficial-pf-v5-wc-icons';

@customElement({
    name: "main-home",
    template: html`${(home: Home) => {
        return html`<div>
            <pf-masthead display-inline>
                <slot slot="brand">
                    <div class="logo"></div>
                </slot>
                <pf-action-list>
                    <pf-action-list-item>
                        <pf-button>Acceuil</pf-button>
                    </pf-action-list-item>
                    <pf-action-list-item>
                        <pf-button>Horaires</pf-button>
                    </pf-action-list-item>
                    <pf-action-list-item>
                        <pf-button>Tarifs</pf-button>
                    </pf-action-list-item>
                    <pf-action-list-item>
                        <pf-button>Connexion</pf-button>
                    </pf-action-list-item>
                </pf-action-list>
            </pf-masthead>
        </div>`
    }}`
})
export class Home extends PfWebComponent{

}

@customElement({
    name: "main-section",
    template: html`${(section: Section) => {
        return html`<section>
            <slot></slot>
    </section>`
    }}`
})
export class Section extends PfWebComponent{

}

@customElement({
    name: "main-footer",
    template: html`${(footer: Footer) => {
        return html`<footer>
            <slot></slot>
    </footer>`
    }}`
})
export class Footer extends PfWebComponent{

}

@customElement({
    name: "custom-modal",
})
export class Modal extends PfModalBox{

}


